import * as Constants from '../constants'

const initialState: State = {
   data: {},
   options: {},
   action:'',
   active: '',
   sideNav:false
};

function reducer(state = initialState, action) {
   switch (action.type) {
      case Constants.SET_FILTER_OBRAS:
         return Object.assign({}, state, {
            action: 'filterObras',
            options: action.option
         });
      case Constants.SET_SEARCH_COLONIAS:
         return Object.assign({}, state, {
            action: 'searchColonias',
            options: action.option
         });
      case Constants.SET_INFO_OBRAS:
         return Object.assign({}, state, {
            action: 'infoObras',
            sideNav: action.option.info,
            data: {
               id: action.option.id,
               nombre: action.option.nombre,
               licitacion: action.option.licitacion,
               descripcio: action.option.descripcio,
               clasifica: action.option.clasifica,
               imagen: action.option.imagen
            }
         });
      default:
         return state;
  }
}

export { reducer, initialState };
