export default function ToolNav(props) {
   const classes = useStyles();
   const theme = useTheme();
  const [single, setSingle] = React.useState(null);
  const [multi, setMulti] = React.useState(null);

   function handleChangeSingle(value) {
      setSingle(value);
      props.setSearchColonias(value.label);
   }

  function handleChangeMulti(value) {
    setMulti(value);
  }

  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  };



    return (
        <nav className={classes.root}>
            <Toolbar>
            <IconButton
                edge="Menu"
                className={classes.menuButton}
                color="inherit"
                aria-label="Menu"
            >
                <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" noWrap>
                <Avatar alt="Alcaldia Azcapolazco" src="/static/logo.jpg" className={classes.avatar}/>
            </Typography>
            <Button color="inherit" onClick = {() => props.setFilterObras('') }>Todas</Button>
            <Button color="inherit" onClick = {() => props.setFilterObras('obra') }>Obras</Button>
            <Button color="inherit" onClick = {() => props.setFilterObras('manif') }>Manifestaciones</Button>
            <Button color="inherit" onClick = {() => props.setFilterObras('parti') }>Publicitaciones</Button>
            <div className={classes.rootAutocomplete}>
               <NoSsr>
                  <Select
                     classes={classes}
                     styles={selectStyles}
                     inputId="react-select-single"
                     TextFieldProps={{

                        InputLabelProps: {
                          htmlFor: 'react-select-single',
                          shrink: true,
                        },
                     }}
                     placeholder="Busqueda de colonias"
                     options={colonias}
                     components={components}
                     value={single}
                     onChange={handleChangeSingle}
                  />
               </NoSsr>
            </div>
            </Toolbar>
        </nav>
    );
}
