import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {withStyles} from '@material-ui/core/styles';
import { connect } from 'react-redux';
import mapboxgl from 'mapbox-gl';
import * as turf from '@turf/turf';

mapboxgl.accessToken = 'pk.eyJ1IjoiZGVsZWdhemNhcG96YWxjbyIsImEiOiJjanl4YjQyemsxOWM1M2RvYXRid201b2Z4In0.XPTnRHlpQyUxenQLwtljuA';

const MapMainStyles = theme => ({
   mapMain: {
      position: 'fixed',
      width: '100%',
      height: '100%',
   }
});

let MapMain = class MapMain extends Component {
   map;
   componentDidMount = () => {
      this.map = new mapboxgl.Map({
         container: this.mapContainer,
         style: 'mapbox://styles/mapbox/navigation-guidance-day-v2',
         center: [-99.19209, 19.48268], // starting position [lng, lat]
         zoom: 12.5
      });

      this.map.on('load', () => {
         this.map.addSource('colonias',{
            "type": 'vector',
            "url": 'mapbox://delegazcapozalco.6p0h53ig'
         });

         this.map.addSource('calles',{
            "type": 'vector',
            "url": 'mapbox://delegazcapozalco.brzuugbi'
         });

         this.map.addSource('obras',{
            "type": 'vector',
            "url": 'mapbox://delegazcapozalco.cjyymskl41rsf2uqm7z976m7l-5wv1u'
         });

         this.map.addLayer({
            "id":'colonias',
            "type": 'fill',
            "source": 'colonias',
            "source-layer": 'colonias-bz3oso',
            "paint": {
               "fill-color": 'rgb(160,60,45)',
               "fill-outline-color": 'rgb(0,0,0)',
               "fill-opacity": 0.5
            }
         });

         this.map.addLayer({
            "id": 'calles',
            "type": 'line',
            "source": 'calles',
            "source-layer": 'calles-9yg4uh',
            "paint":{
               "line-color": 'rgb(140,185,160)',
               "line-opacity": 0.50,
            }
         });

         this.map.addLayer({
            "id":'colonias-light',
            "type": 'line',
            "source": 'colonias',
            "source-layer": 'colonias-bz3oso',
            "paint": {
               "line-color": 'rgb(255,60,45)',
               "line-width": 2.5,
               "line-opacity": 1
            },
            "filter": ["==", "Name", ""]
         });

         this.map.addLayer({
            "id": 'obra',
            "type": 'circle',
            "source": 'obras',
            "source-layer": 'obras',
            "paint":{
               "circle-radius": 4.5,
               "circle-color": [
                  "match",
                  ["get", "clasifica"],
                  ["obra"],
                  "hsl(0, 74%, 44%)",
                  ["manif"],
                  "hsl(110, 49%, 47%)",
                  ["parti"],
                  "hsla(206, 53%, 51%, 0.57)",
                  "#000000"
               ],
               "circle-stroke-width": 1.5,
               "circle-stroke-color": '#000000'
            }
         });
      });


      this.map.on('click', 'obra', (evt) => {
         const props = evt.features[0].properties;

         const dataInfo = {
            info:true,
            id: props.id,
            nombre: props.nombre,
            licitacion: props.licitacion,
            descripcio: props.descripcio,
            clasifica: props.clasifica,
            imagen: props.imagen
         };

         this.props.setInfoObras(dataInfo);
      });

      this.map.on('mouseover', 'obra', () => {
         this.map.getCanvas().style.cursor = 'pointer';
      });

      this.map.on('mouseleave', 'obra', () => {
         this.map.getCanvas().style.cursor = '';
      });


      this.map.addControl(new mapboxgl.NavigationControl());
      this.map.addControl(new mapboxgl.FullscreenControl({container: document.querySelector('body')}));
   }

   componentWillReceiveProps = (nextProps) => {
      if (nextProps.action === 'filterObras'){
         if (nextProps.hasOwnProperty('options')){
            if (nextProps.options === ''){
               this.map.setFilter('obra',  null);
            } else {
               this.map.setFilter('obra',  ["==", "clasifica", nextProps.options]);
            }

         }
      }

      if (nextProps.action === 'searchColonias'){
         var obraFeatures = this.map.querySourceFeatures('colonias', {
            sourceLayer: 'colonias-bz3oso',
            filter: ['==', 'Name', nextProps.options]
         });

         var prop = obraFeatures[0].properties;
         var bbox = turf.bbox(obraFeatures[0].geometry);

         this.map.fitBounds([[bbox[0],bbox[1]],[bbox[2],bbox[3]]]);
         this.map.setFilter('colonias-light', ['==', 'Name', nextProps.options]);
      }


   }

   componentWillUpdate = (nextProps, nextState) => {

   }

   componentDidUpdate = (prevProps, prevState) => {

   }


   render(){
      const { classes } = this.props;
      return(
         <main>
            <div ref={el => this.mapContainer = el} className={classes.mapMain} />
         </main>
      )
   }
}

function mapStateToProps(state) {
   return {
      action: state.action,
      sideNav: state.sideNav,
      options: state.options
   };
}

MapMain = connect(mapStateToProps)(MapMain);


export default withStyles(MapMainStyles)(MapMain);
