import { store } from './store'
import * as Constants from '../constants'

export function SET_FILTER_OBRAS(option) {
   store.dispatch({
      type: Constants.SET_FILTER_OBRAS,
      option
   });
}

export function SET_SEARCH_COLONIAS(option) {
   store.dispatch({
      type: Constants.SET_SEARCH_COLONIAS,
      option
   });
}

export function SET_INFO_OBRAS(option) {
   store.dispatch({
      type: Constants.SET_INFO_OBRAS,
      option
   });
}
