import React,{ Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Route, Switch, Redirect } from 'react-router-dom';

import Header from './components/Header';
import ToolNav from './components/ToolNav';
import MapMain from './components/MapMain';
import SideNav from './components/SideNav';

import { Provider } from 'react-redux';
import { store } from './redux/store'
import { SET_FILTER_OBRAS,SET_INFO_OBRAS,SET_SEARCH_COLONIAS } from './redux/action-creators'

import { withStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import DashboardStyles from './styles/Dashboard';
import logo from './logo.svg';
import './App.css';

function resizeDispatch () {
   if (typeof(Event) === 'function') {
      window.dispatchEvent(new Event('resize'));
   } else {
      const evt = window.document.createEvent('UIEvents');
      evt.initUIEvent('resize', true, false, window, 0);
      window.dispatchEvent(evt);
   }
}

class App extends Component {
   state = {
      opened: true,
      notificationsOpen: false,
      type: 'light',
      direction: 'ltr',
      openSpeedDial: false
   };

   handleDrawerToggle = () => {
      this.setState({ opened: !this.state.opened });
      resizeDispatch()
   };

   handleNotificationToggle = () => {
      this.setState({ notificationsOpen: !this.state.notificationsOpen });
   }

   handleFullscreenToggle = () => {
      const element = document.querySelector('#root');
      const isFullscreen = document.webkitIsFullScreen || document.mozFullScreen || false;

      element.requestFullScreen = element.requestFullScreen || element.webkitRequestFullScreen || element.mozRequestFullScreen || function () { return false; };
      document.cancelFullScreen = document.cancelFullScreen || document.webkitCancelFullScreen || document.mozCancelFullScreen || function () { return false; };
      isFullscreen ? document.cancelFullScreen() : element.requestFullScreen();
   }

   handleSpeedDialOpen = () => {
      this.setState({ openSpeedDial: true });
   };

   handleSpeedDialClose = () => {
      this.setState({ openSpeedDial: false });
   };

   componentDidMount() {

   }

   componentWillUnmount() {

   }

   render() {
      const { classes,sideNav } = this.props;
      const { opened} = this.state;

      return (
         <Provider store={store}>
            <Fragment>
               <ToolNav setFilterObras = { SET_FILTER_OBRAS } setSearchColonias = { SET_SEARCH_COLONIAS }/>
               <Divider className={classes.Divider}/>
               <MapMain setInfoObras = {SET_INFO_OBRAS} />
               <SideNav setInfoObras = {SET_INFO_OBRAS} />
            </Fragment>
         </Provider>
      )
   }
}

App.propTypes = {
   classes: PropTypes.object.isRequired,
};

export default withStyles(DashboardStyles)(App);
